  position: absolute;
  background-color: #fff;
  width: 100%;
  left: 0;
  margin-top: 10px;
  overflow: hidden;
  padding-top: 1rem;
  display: none;
  overflow-y: auto;
  z-index: 9;
  box-shadow: 0px 4px 15px rgba(180, 180, 180, 0.25); }

