  width: 7px;
  /* width of the entire scrollbar */ }

.dropdown-menu ul::-webkit-scrollbar-track {
  background: #F5F7FA;
  /* color of the tracking area */ }

.dropdown-menu ul::-webkit-scrollbar-thumb {
  background-color: #92278F;
