.width-gradient {
  position:relative;

  &:before {
    content: '';
    position:absolute;
    background: #BFDBFF;
    filter: blur(50px);
    transform: translate(-40px, -40px);
    width:140px;
    height:115px;
    top: 0;
    left: 0;
    right: 0;
    margin: 0 auto;
  }
  &:after {
  content: '';
  position:absolute;
  background: #ECC7EB;
  width:135px;
  height:113px;
  filter: blur(50px);
  transform: translate(30px, 0px);
  top: 0;
  left: 0;
  right: 0;
  margin: 0 auto; 
  }
}
