/*счетчик*/
.block-counter {
  margin-top:100px;
}
.counter {

  &__numbers {
    font-size:45px;
    line-height:59px;
    font-weight:500;
    color: #ECC7EB;
    margin-bottom:15px;
  }
}
.map-container {
position:relative;
display:block;
min-height:100%;

& svg {
  position:absolute;
  right:0;
  width:140%;
  height:auto;
  transition:all 0.5s linear;
  }
  & span {
    display: block;
    position: absolute;
    z-index: 15;
    background-color: #F5F7FA;
    color: #838B98;
    font-size: 0.8rem;
    padding: 5px;
    top: 10%;
    opacity:0;
    transition:all 0.5s linear;
  }
  .st0{fill:#FFFFFF;}
  .st1{fill:#FDFEFE;}
  .st2{fill:#561453;}
  .st4{fill:#0BF4F4;}
  .st5{fill:#92278F;}
  .st6{fill:#951B81;}
  .st7{fill:#FFFFFF;stroke:#D871D8;stroke-miterlimit:10;}
  &:hover {
      & svg {
        transform: scale(1.6);
      -webkit-filter: drop-shadow(0 0 11px rgba(146, 39, 143, 0.685));      
      filter: drop-shadow(0 0 11px rgba(146, 39, 143, 0.685));  
      }
      & span {
        opacity:1;
      }

  }
}
// блок рейтинга

.block-rete {
margin-top: 35px;
position:relative;
max-width:100%; 
overflow:hidden;
height:250px;

&:before {
  content: '';
    position: absolute;
    background: #bfdbff;
    filter: blur(50px);
    transform: translate(-20px,-20px);
    width: 70%;
    height: 50px;
    top: 120px;
    left: 0;
    right: 0;
    margin: 0 auto;
}
  &:after {
    content: '';
    position: absolute;
    background: #ecc7eb;
    width: 50%;
    height: 50px;
    filter: blur(50px);
    transform: translate(35%, 10px);
    top:120px;
    left: 0;
    right: 0;
    margin: 0 auto;
  }


& h3 {
  text-transform: none;
  color:#92278F;
  font-size:1.1rem;
}
&__list {
  position: relative;
  display: flex;
  flex-wrap:nowrap;
  justify-content: space-between;
  padding:0;
  margin:0;
  padding-bottom:20px;
  overflow-x:scroll;
  
   & li {
     list-style: none;
     text-align: center;
     min-width:10.5vw;

     & img {
       width:100px;
       height:100px;
       position: relative;
       z-index: 5;
  }
   & p {
     margin-top:15px;
     color: #838B98;
   }
   }

  }
}
